.checkbox{
    margin-top: 35px;
    margin-left: 30px;
}

.checkbox input{
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkbox-container{
    position: relative;
    height: 40px;
    cursor: pointer;
    padding-left: 40px;
    padding-top: 2px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.checkmark{
    position: absolute;
    top: 0;
    left: 0;
    height: 32px;
    width: 32px;
    background-color: #eee;
    border-radius: 10px;
}

.checkbox-container:hover input ~ .checkmark {
    background-color: #5DBCD9;
}

.checkbox-container input:checked ~ .checkmark {
    background-color: #62E395;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
    display: block;
}

.checkbox-container .checkmark:after {
    left: 12px;
    top: 8px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
